import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AttachRutPage } from './attach-rut/attach-rut.page';
import { ProfileOccupationPage } from './home/profile-occupation.page';
import {
  OccupationEconomicActivityPage
} from './occupation-economic-activity/occupation-economic-activity.page';
import {
  ProfileOccupationAddressPage
} from './profile-occupation-address/profile-occupation-address.page';

const routes: Routes = [
  {
    path: '',
    component: ProfileOccupationPage,
  },
  {
    path: 'address',
    component: ProfileOccupationAddressPage,
  },
  {
    path: 'economic-activity',
    component: OccupationEconomicActivityPage,
  },
  {
    path: 'economic-activity/attach-rut',
    component: AttachRutPage,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ProfileOccupationPageRoutingModule {}
