import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { OccupationEntity, OccupationInteractor } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { OccupationService } from '@nequi/features/profile/presentation';
import { ConfirmInfoBottomSheetService, FullscreenModalService } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';

import { OccupationEconomicActivityConfig } from './occupation-economic-activity.config';
import { OccupationEconomicActivityViewModel } from './occupation-economic-activity.view-model';

@Component({
  selector: 'app-occupation-economic-activity',
  templateUrl: './occupation-economic-activity.page.html',
})
export class OccupationEconomicActivityPage extends FeatureBasePage implements OnInit {
  public viewModel = new OccupationEconomicActivityViewModel();
  private occupationData?: OccupationEntity;

  constructor(
    private translateProvider: TranslateProvider,
    private fullscreenModalService: FullscreenModalService,
    private confirmBottomSheetService: ConfirmInfoBottomSheetService,
    private occupationService: OccupationService,
    private occupationInteractor: OccupationInteractor,
    private router: Router,
    injector: Injector
  ) {
    super(injector);

  }

  public ngOnInit() {
    this.occupationData = this.router.getCurrentNavigation().extras.state as OccupationEntity;
    this.viewModel.initForm(this.occupationData);
  }

  public async openConfirm() {
    const items = await this.buildConfirmData();

    const { data } = await this.confirmBottomSheetService.show({
      subtitle: OccupationEconomicActivityConfig.confirmDescriptionKey,
      items,
    });

    if (data) {
      this.occupationService.emit({ ...this.occupationData, ...this.viewModel.model });
    }
  }

  public openDianLink() {
    openUrl(OccupationEconomicActivityConfig.dianUrl);
  }

  public openViewExample() {
    this.fullscreenModalService.show({
      id: OccupationEconomicActivityConfig.viewExample.id,
      imgPath: OccupationEconomicActivityConfig.viewExample.imagePath,
      cssClass: OccupationEconomicActivityConfig.viewExample.cssClass,
    });
  }

  public getCiiuList() {
    return this.occupationInteractor.getCiiuList();
  }

  private async buildConfirmData() {
    const messages = this.translateProvider.get(
      OccupationEconomicActivityConfig.economicActivityKey
    );

    return [
      {
        label: messages['occupation-label'],
        value: this.occupationData?.occupation.name,
      },
      {
        label: messages.title,
        value: this.viewModel.model.ciiuCode.name,
      },
    ];
  }
}
