import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {
  HardwarePermissionService,
  PermissionType
} from '@nequi/apps/base/mobile/config/features/commons/services/hardware-permission';
import {
  CiiuNotMatchException,
  FallenKofaxException,
  NotRutRetriesException,
  OccupationEntity,
  OccupationInteractor
} from '@nequi/features/commons/core';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { OccupationService } from '@nequi/features/profile/presentation';
import { FileChooserPlugin, FileChooserResponse, MimeType } from '@nequi/native/file-chooser';
import { LoadingService } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';

import { AttachRutConfig } from './attach-rut.config';

@Component({
  selector: 'app-attach-rut',
  templateUrl: './attach-rut.page.html',
})
export class AttachRutPage extends FeatureBasePage implements OnInit {
  public config = AttachRutConfig;
  public file: FileChooserResponse;

  private occupationData: OccupationEntity;

  private alertHandlers = {
    [CiiuNotMatchException.name]: () => this.goBack(),
    [FallenKofaxException.name]: () => this.emitOccupationInformation(false),
    [NotRutRetriesException.name]: () => this.finishProcess(),
  };

  constructor(
    private router: Router,
    private fileChooserPlugin: FileChooserPlugin,
    private occupationInteractor: OccupationInteractor,
    private occupationService: OccupationService,
    private alertService: AlertService,
    private loadingService: LoadingService,
    private hardwarePermissionService: HardwarePermissionService,
    injector: Injector
  ) {
    super(injector);
  }

  public ngOnInit() {
    this.occupationData = this.router.getCurrentNavigation().extras.state as OccupationEntity;
  }

  public openDianLink() {
    openUrl(AttachRutConfig.dianUrl);
  }

  public async attachRut() {
    try {
      await this.hardwarePermissionService.check(PermissionType.READ_EXTERNAL_STORAGE);
    } catch (exception) {
      await this.alertService.showByI18n({ i18nKey: this.config.i18n.notHavePermission });

      return;
    }

    this.file = await this.fileChooserPlugin.open(MimeType.PDF);
  }

  public async sendRut() {
    try {
      this.occupationService.notSave = false;
      await this.loadingService.show();
      await this.occupationInteractor.sendRut(this.occupationData?.ciiuCode?.key, this.file.data);
      this.emitOccupationInformation();

      await this.loadingService.hide(true);
    } catch (error) {
      await this.loadingService.hide(false);
      this.occupationService.notSave = true;

      const i18nKey = this.config.i18n.rutErrors[error.constructor.name];
      const alertHandler = this.alertHandlers[error.constructor.name] || null;

      if (!i18nKey) {
        this.emitOccupationInformation(false);

        throw error;
      }

      await this.alertService.showByI18n({ i18nKey, handlers: { accept: alertHandler } });
    }
  }

  private emitOccupationInformation(validate: boolean = true) {
    this.occupationService.emit({
      ...this.occupationData,
      validate,
    });
  }

  private finishProcess() {
    this.navigatorProvider.navigateRoot(AttachRutConfig.goTo.home);
  }
}
