import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { OccupationEntity } from '@nequi/features/commons/core';
import { CustomInputValidators, FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { OccupationService } from '@nequi/features/profile/presentation';
import { ConfirmInfoBottomSheetService } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { ProfileOccupationAddress } from './profile-occupation-address.config';
import { ProfileOccupationAddressViewModel } from './profile-occupation-address.view-model';

@Component({
  selector: 'app-profile-agreement',
  templateUrl: './profile-occupation-address.page.html',
})
export class ProfileOccupationAddressPage extends FeatureBasePage implements OnInit {
  public viewModel = new ProfileOccupationAddressViewModel();
  public patterns = CustomInputValidators.patterns;

  private occupationData?: OccupationEntity;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private confirmInfoBottomSheetService: ConfirmInfoBottomSheetService,
    private occupationService: OccupationService,
    private router: Router
  ) {
    super(injector);
  }

  public ngOnInit() {
    this.occupationData = this.router.getCurrentNavigation().extras.state as OccupationEntity;
    this.viewModel.initForm(this.occupationData?.location);
  }

  public async openConfirmBottomSheet() {
    const confirmData = await this.buildConfirmData();
    const result = await this.confirmInfoBottomSheetService.show({ items: confirmData });

    if (result.data === true) {
      this.occupationService.emit({
        ...this.occupationData,
        location: {
          ...this.viewModel.model,
        },
      });
    }
  }

  private async buildConfirmData() {
    const messages = this.translateProvider.get(ProfileOccupationAddress.i18n.profileOccupation);

    return [
      {
        label: this.occupationData?.occupation.name,
        value: this.occupationData?.profession,
      },
      {
        label: messages.address.bottomSheet.province,
        value: this.viewModel.model.province.name,
      },
      {
        label: messages.address.bottomSheet.city,
        value: this.viewModel.model.city.name,
      },
      {
        label: messages.address.bottomSheet.address,
        value: createFormat(messages.address.bottomSheet['address-format'], [
          this.viewModel.model.address.road.name,
          this.viewModel.model.address.firstPart,
          this.viewModel.model.address.secondPart,
        ]),
      },
      {
        label: messages.address.bottomSheet.details,
        value: this.viewModel.model.address.extraPart,
      },
    ];
  }
}
