import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import {
  NotRequireEconomicActivityException,
  OccupationCode,
  OccupationEntity,
  OccupationInteractor,
  RequireDescriptionException,
  RequireEconomicActivityException,
  RequireLocationException
} from '@nequi/features/commons/core';
import { DynatraceService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { OccupationService } from '@nequi/features/profile/presentation';

import { ProfileOccupationConfig } from './profile-occupation.config';
import { ProfileOccupationViewModel } from './profile-occupation.view-model';

@Component({
  selector: 'app-profile-occupation',
  templateUrl: './profile-occupation.page.html',
})
export class ProfileOccupationPage extends FeatureBasePage implements OnInit {
  public viewModel = new ProfileOccupationViewModel();

  private data: OccupationEntity;

  public get descriptionPlaceholder(): string {
    return ProfileOccupationConfig[this.viewModel.model.occupation.code];
  }

  constructor(
    private occupationInteractor: OccupationInteractor,
    private occupationService: OccupationService,
    private route: ActivatedRoute,
    private router: Router,
    private dynatraceService: DynatraceService,
    injector: Injector
  ) {
    super(injector);
  }

  public ngOnInit(): void {
    this.data = this.router.getCurrentNavigation().extras.state as OccupationEntity;

    this.viewModel.initForm(this.data);
    this.onOccupationChanged();
  }

  public getOccupationList() {
    return this.occupationInteractor.get();
  }

  public onOccupationChanged() {
    try {
      this.viewModel.removeProfessionFormControl();
      this.occupationInteractor.validateRequireDescription(this.viewModel.model);
    } catch (exception) {
      this.manageRequireDescriptionException(exception);
    }
  }

  public onOccupationFilled() {
    try {
      this.occupationInteractor.validateOccupation(this.viewModel.model);
      this.occupationService.emit({ ...this.data, ...this.viewModel.model });
    } catch (exception) {
      this.manageRequireLocationException(exception);
      this.manageRequireEconomicActivityException(exception);
      this.manageNotRequireEconomicActivityException(
        exception,
        this.viewModel.model.occupation.code
      );

      this.dynatraceService.setCustomError(exception, null, true);
    }
  }

  private manageRequireDescriptionException(exception: CoreException) {
    if (exception instanceof RequireDescriptionException) {
      this.viewModel.addProfessionFormControl(this.data);
    }

    this.dynatraceService.setCustomError(exception, null, true);
  }

  private manageRequireLocationException(exception: CoreException) {
    if (exception instanceof RequireLocationException) {
      this.navigatorProvider.navigateForward(['address'], {
        relativeTo: this.route,
        state: {
          ...this.data,
          ...this.viewModel.model,
        },
      });
    }
  }

  private manageRequireEconomicActivityException(exception: CoreException) {
    if (exception instanceof RequireEconomicActivityException) {
      this.navigatorProvider.navigateForward(['economic-activity'], {
        relativeTo: this.route,
        state: {
          // TODO: validate if manage one data from view-model
          ...this.data,
          ...this.viewModel.model,
        },
      });
    }
  }

  private manageNotRequireEconomicActivityException(
    exception: CoreException,
    occupationCode: OccupationCode
  ) {
    if (!(exception instanceof NotRequireEconomicActivityException)) {
      return;
    }

    if (this.occupationInteractor.validateIsRentier(occupationCode)) {
      const RENTIER_CAPITAL = '0090';

      this.viewModel.model.ciiuCode = {
        key: RENTIER_CAPITAL,
        name: null,
      };
    }

    this.occupationService.emit({ ...this.data, ...this.viewModel.model });
  }
}
