import { FormControl, FormGroup, Validators } from '@angular/forms';
import { OccupationCode, OccupationEntity } from '@nequi/features/commons/core';

const PROFESSION = 'profession';

export class ProfileOccupationViewModel {
  public form: FormGroup;
  public occupationFormControl: FormControl;
  public professionFormControl: FormControl;

  public get model(): OccupationEntity {
    return this.form.value;
  }

  public initForm(data?: OccupationEntity) {
    this.createControls(data);
    this.createFormGroup(data);
  }

  public addProfessionFormControl(occupation: OccupationEntity) {
    this.professionFormControl = new FormControl(occupation?.profession ?? '');

    if (occupation?.occupation.code !== OccupationCode.STUDENT) {
      this.professionFormControl.setValidators(Validators.required);
    }

    this.form.addControl(PROFESSION, this.professionFormControl);
  }

  public removeProfessionFormControl() {
    this.professionFormControl = null;
    this.form.removeControl(PROFESSION);
  }

  private createControls(data: OccupationEntity) {
    this.occupationFormControl = new FormControl(data?.occupation ?? '', [Validators.required]);
  }

  private createFormGroup(data: OccupationEntity) {
    this.form = new FormGroup({
      occupation: this.occupationFormControl,
    });

    if (data?.profession) {
      this.addProfessionFormControl(data);
    }

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }
}
