import {
  AbstractControl,
  FormControl,
  FormGroup,
  ValidationErrors,
  Validators
} from '@angular/forms';
import { CompanyLocationEntity, LocationEntity } from '@nequi/features/commons/core';
import { AddressOutput } from '@nequi/features/commons/presentation';

export class ProfileOccupationAddressViewModel {
  public formGroup: FormGroup;
  public form: {
    company: FormControl;
    address: FormControl;
  };

  private isAddressValid: boolean;

  public get address(): LocationEntity {
    return this.formGroup.value.address;
  }

  public get model(): CompanyLocationEntity {
    return {
      companyName: this.formGroup.value.company,
      ...this.formGroup.value.address,
    };
  }

  public initForm(data?: CompanyLocationEntity) {
    this.form = {
      company: new FormControl(data?.companyName, [Validators.required]),
      address: new FormControl(data, [this.addressFilled.bind(this)]),
    };

    this.formGroup = new FormGroup({
      ...this.form,
    });
  }

  public onAddressChanged(value: AddressOutput): void {
    this.isAddressValid = value.isValidForm;
    this.form.address.setValue(value.data);
  }

  private addressFilled(control: AbstractControl): ValidationErrors | null {
    if (!control.value || !this.isAddressValid) {
      return { required: true };
    }
  }
}
